//mock.js 
import Mock from 'mockjs' // mockjs
const Random = Mock.Random // Mock.Random 

interface DataList {
  date: string,
  name: string,
  address: string
}

const dataList: DataList[] = [] // 
for (let i = 0; i < 100; i++) { // 
  const template = {
    date: Random.date(), // ,
    name: Random.name(), // 
    address: Random.province() // 
  }
  dataList.push(template)
}
// list ()

Mock.mock('/api/list', 'post', (params: any) => {
  let info = JSON.parse(params.body)
  let [index, size, total] = [info.current, info.pageSize, dataList.length]
  let len = total / size
  let totalPages = len - parseInt(String(len)) > 0 ? parseInt(String(len)) + 1 : len
  let newDataList = dataList.slice(index * size, (index + 1) * size)
  return {
    'code': '200',
    'message': '',
    'data': {
      'current': index,
      'pageSize': size,
      'rows': newDataList,
      'total': total,
      'totalPages': totalPages
    }
  }
})
